.TechStack {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.TechStack .technos {
  display: flex;
}
.TechStack .technos img {
  height: 200px;
  margin: 50px;
}
.TechStack .technos img:hover {
  filter: blur(4px);
  opacity: 0.5;
}
.TechStack .technos .img-span-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.TechStack .technos .img-span-container span {
  position: absolute;
  font-size: 1.3em;
  font-weight: bold;
}
@media (max-width: 1100px) {
  .TechStack .technos {
    display: flex;
  }
  .TechStack .technos img {
    height: 100px;
    margin: 0 35px;
  }
}
@media (max-width: 575px) {
  .TechStack .technos {
    display: flex;
  }
  .TechStack .technos img {
    height: 75px;
    margin: 0 18px;
  }
}/*# sourceMappingURL=TechStack.css.map */