.ProjectPreview{
    border: 1px solid black;
    border-radius: 40px;
    width: 25%;
    aspect-ratio: 1/1;
    margin: 40px;
    background-color: white;
    .preview-media{
        width: 100%;
        height: 75%;
        border-top-left-radius: 40px;
        border-top-right-radius: 40px;
        border-bottom: 1px solid black;
    }

    .content{
        margin: 20px;
        .name{
            font-size: 1.4em;
            font-weight: 700;
            margin: 7px auto;
        }
        .desc{
            margin: 0 auto;
        }
    }
    
    @media (max-width: 768px){
        width: 100%;
        margin: 40px 0;
    }
}